import React from 'react';

import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

const Footer = () => {
  return (
    <Box p={1} bgcolor="#AD8B73">
      <Typography textAlign={'center'} variant="body1" component={'h1'}>
        © ericodwr
      </Typography>
    </Box>
  );
};

export default Footer;
