import React from 'react';

import Typography from '@mui/material/Typography';

const Title = ({ title, subTitle, small, project }) => {
  return (
    <>
      <Typography
        variant={small ? 'h6' : 'h4'}
        component={'h1'}
        letterSpacing={small ? 0.5 : 2}
        textAlign={project ? '' : 'center'}
        fontWeight={project ? '500' : ''}
        pt={project ? 0 : 5}
        textTransform="uppercase"
        gutterBottom
      >
        {title}
      </Typography>
      {subTitle && (
        <Typography
          variant="h6"
          component={'h2'}
          letterSpacing={1}
          textAlign="center"
          gutterBottom
        >
          {subTitle}
        </Typography>
      )}
    </>
  );
};

export default Title;
